import { Component, OnInit } from '@angular/core';
import { FileUploader, FileUploadModule } from 'ng2-file-upload';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Router } from '@angular/router';
import { UserSeviceService } from '../user-sevice.service';
import { CookieService } from 'ngx-cookie-service';
import { GlobalConstService } from '../global-const.service';
import { stringify } from '@angular/core/src/render3/util';
import { GlobalInfoService } from '../global-info.service';

@Component({
  selector: 'app-self-info-page',
  templateUrl: './self-info-page.component.html',
  styleUrls: ['./self-info-page.component.css']
})
export class SelfInfoPageComponent implements OnInit {
  constructor(
    public http: HttpClient,
    private router: Router,
    private userService: UserSeviceService,
    private cookieService: CookieService,
    private globalConst: GlobalConstService,
    private globalInfo: GlobalInfoService
  ) { }
  isSelectingPicture = 0;
  ngOnInit() {
    this.userService.cookiesJudge();
  }
  closeSelectWindow(isShow) {
    this.isSelectingPicture = isShow;
  }
  transform(transTime) {
    const date = new Date(transTime);
    const year = date.getFullYear();
    const month = date.getMonth() + 1;
    const d = date.getDate();
    let dString = '' + d;
    let mString = '' + month;
    if (month < 10) {
      mString = '0' + mString;
    }
    if (d < 10) {
      dString = '0' + dString;
    }

    return year + '-' + mString + '-' + dString ;
  }
  birthdayChange(e) {
    // e.target.value = '0001-05-04';
    // 字符串构建date

    this.globalInfo.tempUserInfo.birthday = new Date(e.target.value);
  }

  changeInfo() {
    // 判断用户名,生日,性别是否合法的逻辑还没写...

    console.log(this.globalInfo.tempUserInfo);
    this.userService.setInformation(this.globalInfo.tempUserInfo).subscribe( (res) => {
      if ( res === 1) {
        console.log('修改成功');
        this.userService.correction();
      } else {
        console.log('修改失败');
      }
    });

  }
  goToSelectPicture() {
    this.isSelectingPicture = 1;
  }
}
