import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams, } from '@angular/common/http';
import { Router } from '@angular/router';
import { CookieService } from 'ngx-cookie-service';
import { DomSanitizer, SafeUrl } from '@angular/platform-browser';
import { FileUploader } from 'ng2-file-upload';
import { async } from '@angular/core/testing';
import { delay } from 'q';
import { MessageService } from './message.service';
import { GlobalInfoService } from './global-info.service';
import { GlobalConstService } from './global-const.service';
@Injectable({
  providedIn: 'root'
})
export class UserSeviceService {

  constructor(
    public http: HttpClient,
    private router: Router,
    private cookieService: CookieService,
    private sanitizer: DomSanitizer,
    private globalConst: GlobalConstService,
    private globalInfo: GlobalInfoService
  ) { }

  updatePicture() {
    if (this.globalInfo.uploader.queue[0] === undefined) {
      // 用户没上传文件时
      return;
    }
    this.globalInfo.uploader.queue[0].file.name = this.globalInfo.tempUserInfo.pictureUrl;
    this.globalInfo.uploader.queue[0].upload(); // 开始上传
    this.globalInfo.uploader.queue[0].onSuccess = (res) => {
      // 上传文件成功时执行下面
        if (Number(res) === 1) {
            console.log('OK');
            this.globalInfo.userInfo.pictureUrl = this.globalInfo.tempUserInfo.pictureUrl;
            this.setInformation(this.globalInfo.userInfo).subscribe((res1) => {
              // 下面是修改信息成功后的逻辑
              if ( res1 === 1) {
                console.log('修改成功');
                this.correction();
              } else {
                console.log('修改失败');
              }
            });
        } else {
            console.log('NO');
        }
    };
    this.globalInfo.uploader.queue = []; // 由于queue是可以容纳多个文件的,每次在picture-upload-tool中打开一个文件,这个队列
                              // 就会多一个元素,所以清空它,保证下次上传时,里面只有一个元素
  }

  cookiesJudge() {
    console.log(this.cookieService.getAll);
    if (Number(this.cookieService.get('uid')) === 0) {
      console.log('你还没登录\n');
      this.router.navigate(['login']);
    } else {
      this.globalInfo.userInfo.uid = Number(this.cookieService.get('uid'));
      console.log('欢迎用户:' + this.globalInfo.userInfo.uid + '的访问!\n');
      this.showInitialInformation();
    }
  }
  showInitialInformation() {
    this.getUserInfoRequest(this.globalInfo.userInfo.uid).subscribe(
      (res: any) => {
        // 当flea_user_information没有这个id的时候,执行下面操作
        if (res.uid === 0) {
          this.setBlankUserInfoRequest().subscribe((res1: any) => {
            console.log(res1); // 1表示成功 0表示失败
            if (res1 === 1) {
              console.log('设置空白信息成功');
              this.correction();
            } else {
              console.log('设置空白信息失败');
            }
          });
        } else {
          this.globalInfo.userInfo = res;
          this.correction();
        }
      }
    );
  }
  correction() {
    this.getUserPictureRequest(this.globalInfo.userInfo.uid).subscribe(
      (res: Blob) => {
      this.globalInfo.userInfo.picture = this.sanitizer.bypassSecurityTrustUrl(URL.createObjectURL(res));
      this.globalInfo.tempUserInfo = this.globalInfo.userInfo;
      this.globalInfo.messageInfo.uid = this.globalInfo.userInfo.uid;
      console.log('您的用户信息是:', this.globalInfo.userInfo);
      console.log('您的临时信息是:', this.globalInfo.tempUserInfo);
      console.log('您的留言信息是:', this.globalInfo.messageInfo);
    });
  }

  getUserInfoRequest(auid) {
    const header = new HttpHeaders({
      'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
    });
    const request = { headers: header };
    return this.http.post(this.globalConst.serverUrl + '/Server/getUserInformation', {uid: auid}, request);
  }
  searchInfo(uid) {
    for (const item of this.globalInfo.infoStorer) {
      if (uid === item.uid) {
        return item;
      }
    }
    return null;
  }

  setBlankUserInfoRequest() {
      const header = new HttpHeaders({
        'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
      });
      const request = { headers: header };
      return this.http.post(this.globalConst.serverUrl + '/Server/setBlankInformation', this.globalInfo.userInfo, request);
  }

  getUserPictureRequest(auid) {
    const header = new HttpHeaders({
       'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8',
    });
    return this.http.post(this.globalConst.serverUrl + '/Server/getUserPicture', {uid: auid}, { headers: header, responseType: 'blob'});
  }

  setInformation(info) {
    const header = new HttpHeaders({
      'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8',
    });
    return this.http.post(this.globalConst.serverUrl + '/Server/setInformation', info, { headers: header });
  }
}



