import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginPageComponent } from './login-page/login-page.component';
import { RegisterPageComponent } from './register-page/register-page.component';
import { RegisterResponsePageComponent } from './register-response-page/register-response-page.component';
import { HomePageComponent } from './home-page/home-page.component';
import { SelfInfoPageComponent } from './self-info-page/self-info-page.component';

const routes: Routes = [
  {
    path: '',
    component: LoginPageComponent
  },
  {
    path: 'login',
    component: LoginPageComponent
  },
  {
    path: 'register',
    component: RegisterPageComponent
  },
  {
    path: 'registerResponse',
    component: RegisterResponsePageComponent
  },
  {
    path: 'home',
    component: HomePageComponent
  },
  {
    path: 'selfInfo',
    component: SelfInfoPageComponent
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
