import { Injectable } from '@angular/core';
import { UserSeviceService } from './user-sevice.service';
import { DomSanitizer } from '@angular/platform-browser';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { GlobalInfoService } from './global-info.service';
import { GlobalConstService } from './global-const.service';

@Injectable({
  providedIn: 'root'
})
export class MessageService {

  constructor(
    private userService: UserSeviceService,
    private globalInfo: GlobalInfoService,
    private globalConst: GlobalConstService,
    private sanitizer: DomSanitizer,
    public http: HttpClient,

  ) { }
  process() {
    this.globalInfo.processMessages = [];
    for (const x of this.globalInfo.messages) {

      this.userService.getUserPictureRequest(x.uid).subscribe( (res: any) => {
        this.userService.getUserInfoRequest(x.uid).subscribe((res1: any) => {
          const unit = {
            time: x.time,
            context: x.context,
            picture: this.sanitizer.bypassSecurityTrustUrl(URL.createObjectURL(res)),
            userName:  res1.userName,
          };
          this.globalInfo.processMessages.push(unit);
        });
      });
    }
    console.log(this.globalInfo.infoStorer);
  }

  writeMessageRequest(messageInfo) {
    const header = new HttpHeaders({
      'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8',
    });
    return this.http.post(this.globalConst.serverUrl + '/Server/writeMessage', messageInfo, { headers: header});
  }

  getWholeMessage() {
    this.getWholeMessageRequest().subscribe((res) => {
      this.globalInfo.messages = res;
      console.log('所有留言', this.globalInfo.messages);
      this.process();
    });
  }

  getWholeMessageRequest() {
    const header = new HttpHeaders({
      'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8',
    });
    return this.http.post(this.globalConst.serverUrl + '/Server/getWholeMessage', {}, { headers: header});
  }
}
